/* RANGE SLIDER STYLES */
.range-slider { color: #BABCBE; width: 280px; margin: 10px auto 10px; }
.input-range { vertical-align: bottom; }

.range-value { font-size: 52px; display: block; text-align: center;}

	/* CHROME */
	input[type=range]{
		 -webkit-appearance: none;
		margin: 0;
		border: 0 none;
		padding: 0;
	}
	input[type=range]::-webkit-slider-runnable-track {
		 width: 100%;
		 height: 3px;
		 background: #d3d8e5;
		 border: none;
		 border-radius: 3px;
	}
	input[type=range]::-webkit-slider-thumb {
		 -webkit-appearance: none;
		 border: none;
		 height: 18px;
		 width: 18px;
		 border-radius: 50%;
		 background: #264cda;
		 margin-top: -8px; border: 4px solid #fff;box-shadow: 0px 4px 12px -5px rgba(0,0,0,0.75);
	}
	input[type=range]:focus {
		 outline: none;
	}
	input[type=range]:focus::-webkit-slider-runnable-track {
		 background: #BABCBE;
	}

	/* FIREFOX */
	input[type=range]{
		 /* fix for FF unable to apply focus style bug  */
		 border: 0 solid white; 

		 /*required for proper track sizing in FF*/
		 width: 280px;
	}
	input[type=range]::-moz-range-track {
    width: 100%;
    height: 3px;
    background: #d3d8e5;
    border: none;
    border-radius: 3px;
	}
	input[type=range]::-moz-range-thumb {
    -webkit-appearance: none;
    border: none;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    background: #264cda;
    margin-top: -8px; border: 4px solid #fff;box-shadow: 0px 4px 12px -5px rgba(0,0,0,0.75);
	}

	/*hide the outline behind the border*/
	input[type=range]:-moz-focusring{
		 outline: 1px solid white;
		 outline-offset: -1px;
	}
	input[type=range]:focus::-moz-range-track {
		 background: #BABCBE;
	}

	/* IE10 */
	input[type=range]::-ms-track {
		 width: 280px;
		 height: 5px;

		 /*remove bg colour from the track, we'll use ms-fill-lower and ms-fill-upper instead */
		 background: transparent;

		 /*leave room for the larger thumb to overflow with a transparent border */
		 border-color: transparent;
		 border-width: 0px 0;

		 /*remove default tick marks*/
		 color: transparent;
	}
	input[type=range]::-ms-fill-lower {
		 background: #777;
		 border-radius: 10px;
	}
	input[type=range]::-ms-fill-upper {
		 background: #ddd;
		 border-radius: 10px;
	}
	input[type=range]::-ms-thumb {
    -webkit-appearance: none;
    border: none;
    height: 18px;
    width: 18px;
    border-radius: 50%;
    background: #264cda;
    margin-top: -8px; border: 4px solid #fff;box-shadow: 0px 4px 12px -5px rgba(0,0,0,0.75);
	}
	input[type=range]:focus::-ms-fill-lower {
		 background: #888;
	}
	input[type=range]:focus::-ms-fill-upper {
		 background: #ccc;
  }
